*, :after, :before {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  margin: 0;
}

body {
  font-family: system-ui;
}

:root {
  --margin-left: 1.5rem;
}

main {
  height: 100%;
  width: 100%;
  background-color: #fff;
  padding: 0;
}

header, footer {
  align-items: center;
  column-gap: var(--margin-left);
  display: flex;
}

header {
  z-index: 1;
  width: calc(100% - .5rem);
  padding: 0 var(--margin-left);
  height: 5rem;
  position: absolute;
  top: .25rem;
  left: .25rem;
}

header #title {
  text-align: right;
  flex-grow: 1;
}

header #logo-epfl {
  margin-top: -8px;
}

footer {
  z-index: 1;
  padding: .5rem var(--margin-left);
  width: calc(100% - .5rem);
  margin: 0;
  position: absolute;
  bottom: .25rem;
  left: .25rem;
}

footer #author {
  flex-grow: 1;
}

footer #logo-uzufly, footer #logo-github {
  vertical-align: middle;
}

footer #logo-github {
  margin-top: calc(1rem - 20px);
}

.glassy {
  background: #fffc;
}

@supports ((-webkit-backdrop-filter: none)) or ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
  .glassy {
    -webkit-backdrop-filter: saturate(180%) blur(5px);
    -webkit-backdrop-filter: saturate(180%) blur(5px);
    backdrop-filter: saturate(180%) blur(5px);
  }
}

h1, p {
  margin-block: 0 .5rem;
}

.no-space {
  margin-block: 0;
}

a {
  color: #000;
  font-weight: bold;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

cesium-ifc-viewer {
  height: 100%;
  background-color: #fff;
  border: .25rem solid #fff;
}

cesium-ifc-viewer::part(slotted), cesium-ifc-viewer::part(msg) {
  z-index: 1;
  position: absolute;
}

cesium-ifc-viewer::part(slotted) {
  left: var(--margin-left);
  color: #fff;
  background-color: red;
  border-radius: 1.5rem;
  padding: .75rem 1.5rem;
  top: 7rem;
}

cesium-ifc-viewer::part(msg) {
  min-height: 10rem;
  color: #000;
  background-color: #fff;
  border: .25rem solid #000;
  padding: .5rem 1rem;
  display: flex;
  bottom: 2rem;
}

